  margin: 0;
  box-sizing: border-box;
}

.containerInput {
  top: 50%;
  left: 50%;
  height: 500px;
  width: 1000px;
  position: absolute;
  transform: translate(-50%, -50%);
  background: #ffffff;
  background: var(--text_light);
}

.containerInput {
  box-shadow: 0px 30px 40px black;
  display: flex;
  border-radius: 10px;
}

.image {
  flex: 50% 1;
  background: linear-gradient(45deg, #fe6b8b 30%, #ff8e53 90%);
  background-size: cover;
  text-align: center;
  color: white;
}

.image h1 {
  margin-top: 50%;
  letter-spacing: 2px;
}

.welcome {
  color: #000;
  color: var(--text_dark);
}

.content {
  flex: 50% 1;
  background-color: #181a19;
  background-color: var(--bg_color_dark);
  text-align: center;
}

.content h1 {
  padding-top: 10px;
  padding: 20px;
  font-size: 30px;
  color: #ffffff;
  color: var(--text_light);
}

.content h3 {
  padding-top: 15px;
  font-size: 20px;
  color: #ffffff;
  color: var(--text_light);
}

label {
  font-weight: bold;
  font-size: small;
}

.fp {
  text-decoration: none;
  font-weight: bold;
  font-size: small;
  transition: 0.3s;
  color: var(--blue);
}

.fp:hover {
  color: rgba(167, 240, 107, 0.966);
  color: var(--green);
}

.form-group {
  color: #fff;
}

.form-group-signup {
  margin-top: 5px;
}

hr {
  margin-top: 20px;
}

button a {
  text-decoration: none;
  color: white;
}

.login {
  margin-top: 15px;
  margin-bottom: 10px;
  color: #ffffff;
  color: var(--text_light);
  border-radius: 10px;
  font-weight: bold;
  font-size: small;
}
.links {
  display: flex;
  justify-content: space-around;
  margin: 20px;
  cursor: pointer;
  color: #000;
  color: var(--text_dark);
}

.links-signup {
  margin: 15px;
}

.facebook,
.instagram,
.google {
  height: 45px;
  line-height: 45px;
}
.facebook {
  width: 30%;
  background: #6bacd8cc;
  background: var(--facebook);
  border: 1px #6bacd8cc;
  border: 1px var(--facebook);
}
.instagram {
  right: 0;
  width: 30%;
  background: hsl(348, 35%, 74%);
  background: var(--insta);
  border: 1px solid hsl(348, 35%, 74%);
  border: 1px solid var(--insta);
}
.facebook:hover {
  background: #3e61a8;
}
.instagram:hover {
  background: hsl(348, 11%, 91%);
  background: var(--cream);
}

.google {
  right: 0;
  width: 30%;
  background: rgb(212, 201, 201);
  border: 1px solid rgb(212, 201, 201);
}
.google:hover {
  background: lightgray;
  border: 1px solid lightgray;
}

.links i {
  font-size: 13px;
  margin: 0 auto;
}
i span {
  margin-left: 8px;
  font-weight: 500;
}
.signup {
  margin: 40px;
  font-size: 13px;
  color: #ffffff;
  color: var(--text_light);
}
.signup a {
  color: var(--blue);
  text-decoration: none;
}
.signup a:hover {
  text-decoration: underline;
}

button a:hover {
  color: #ffffff;
  color: var(--text_light);
}

.signup-signup {
  margin: 10px;
}

@media screen and (max-width: 1000px) {
