@media screen and (max-width: 768px) {
  .logo a {
    margin: 8px;
  }

  .hamburger {
    position: absolute;
    right: 5%;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    z-index: 3;
  }

  .line {
    display: block;
    height: 3px;
    width: 20px;
    margin: 4px;
    border-radius: 3px;
    background-color: white;
  }

  .nav-items {
    position: fixed;
    height: 100vh;
    width: 100vw;
    background-color: #ff6961;
    flex-direction: column;
    -webkit-clip-path: circle(1px at 85% -10%);
    clip-path: circle(1px at 85% -10%);
    transition: all 0.8s ease-out;
    pointer-events: none;
    z-index: 2;
  }
  .nav-items.show {
    display: flex;
    -webkit-clip-path: circle(1000px at 85% -20%);
    clip-path: circle(2000px at 100% -20%);
    pointer-events: all;
  }

  a {
    font-size: 25px;
  }

  .nav-search {
    display: flex;
    justify-content: end;
    padding-bottom: 5px;
  }

  .nav-item {
    opacity: 100%;
  }

  .search-field {
    width: 75%;
  }

  .nav-item:nth-child(1).open {
    transition: all 0.5s ease 0.2s;
    margin-top: 30%;
  }
  .nav-item:nth-child(2).open {
    transition: all 0.5s ease 0.4s;
  }
  .nav-item:nth-child(3).open {
    transition: all 0.5s ease 0.6s;
  }

  .nav-item:nth-child(4).open {
    transition: all 0.5s ease 0.8s;
    margin-bottom: 30%;
    margin-left: 3%;
  }

  .nav-item.fade {
    opacity: 1;
  }
}

