  --text_dark: #000;
  --cream: hsl(348, 11%, 91%);
  --insta: hsl(348, 35%, 74%);
  --facebook: #6bacd8cc;
  --green: rgba(99, 250, 39, 0.966);
  --grey: rgb(44, 44, 44);
  --messageChats: rgb(153, 159, 160);
}

.DIRECT__page {
  width: 100%;
  height: 100%;
}

.DIRECT__component {
  font-size: 90%;
  background-color: white;
  border-radius: 0.25rem;
  display: flex;
  overflow: hidden;
  color: #000;
  color: var(--text_dark);
  min-height: 740px;
  width: 80%;
  height: 100px;
  margin: 3% auto;
}

.DIRECT__left_side {
  flex: 0.3 1;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  overflow: auto;
  color: #ffffff;
  color: var(--text_light);
  background: #181a19;
  background: var(--bg_color_dark);
}

.DIRECT__right_side {
  background: #000;
  background: var(--text_dark);
  flex: 0.7 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow-y: auto;
}

.CHAT_ROW__container {
  width: 100%;
  height: 100%;
  padding: 10px;
  max-height: 70px;
  display: flex;
  flex-direction: row;
}

.CHAT_ROW__container_active {
  background-color: rgb(44, 44, 44);
  background-color: var(--grey);
}

.CHAT_ROW__container:hover {
  cursor: pointer;
}

.hr_chat {
  margin: 0;
}

.CHAT_ROW_TITLE {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.CHAT_ROW_ONLINE {
  size: 2px;
  margin-left: 3px;
  color: rgba(99, 250, 39, 0.966);
  color: var(--green);
}

.CHAT_ROW__left {
  flex: 0.1 1;
}

.CHAT_ROW__left img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-position: center;
  object-fit: cover;
}

.CHAT_ROW__center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: baseline;
  padding: 10px;
  overflow-wrap: break-word;
  word-wrap: break-word;
  width: 260px;
}

.CHAT_ROW__center h5 {
  padding-top: 4px;
  color: rgb(153, 159, 160);
  color: var(--messageChats);
  font-style: italic;
}

.CHAT_ROW__right {
  display: flex;
  flex-direction: column;
  min-width: 57px;
  font-size: 0.8rem;
  align-items: flex-end;
  justify-content: space-around;
  margin-left: auto;
}

@media screen and (max-width: 1000px) {
  .DIRECT_SIDE_NOT_ACTIVE {
    display: none;
