  .DIRECT__right_side {
    flex: 1 1;
    /* max-height: 800px; */
  }

  .DIRECT__left_side {
    flex: 1 1;
  }

  .DIRECT__component {
    font-size: 90%;
    background-color: white;
    border-radius: 0.25rem;
    /* height: 100%; */
    width: 80%;
    overflow-y: auto;
