  height: 50px;
  border-radius: 50%;
  object-position: center;
  object-fit: cover;
}

.MESSAGES__body {
  padding: 10px;
  height: 100%;
  overflow: auto;
  background: rgb(44, 44, 44);
  background: var(--grey);
}

.MESSAGES__send {
  display: flex;
  flex-direction: row;
  align-items: center;
