.MESSAGES_SEND:hover {
  cursor: pointer;
}

.MESSAGES__chat-msg {
  font-family: 'Manrope', sans-serif;
  font-family: var(--body-font);
  display: flex;
  padding: 0 20px 10px;
}
.MESSAGES__chat-msg-content {
  margin-left: 12px;
  word-break: break-word;
  max-width: 70%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.MESSAGES__chat-msg-text {
  display: flex;
  flex-direction: column;
  align-items: inherit;
  color: white;
  background-color: #181a19;
  background-color: var(--bg_color_dark);
  padding: 10px;
  border-radius: 20px 20px 20px 0;
  line-height: 1.5;
  font-size: inherit;
  font-weight: 500;
}
.MESSAGES__chat-msg-text + .chat-msg-text {
  margin-top: 10px;
}

.MESSAGES__chat-msg-date {
  color: #6c6c6c;
  color: var(--lightgrey);
  font-size: 0.7rem;
  display: flex;
  justify-content: end;
}

.MESSAGES__owner {
  flex-direction: row-reverse;
}

.MESSAGES__owner .MESSAGES__chat-msg-date {
  color: #ebe6e7;
  color: var(--bg_color_light);
}

.MESSAGES__owner .MESSAGES__chat-msg-content {
  margin-left: 0;
  margin-right: 12px;
  align-items: flex-end;
}
.MESSAGES__owner .MESSAGES__chat-msg-text {
  background-color: #0086ff;
  background-color: var(--theme-color);
  color: #fff;
  border-radius: 20px 20px 0 20px;
}
.MESSAGES__owner .MESSAGES__chat-msg-date {
  left: auto;
  right: calc(100% + 12px);
}

.MESSAGES_btn {
  margin-top: 0;
}

